#quote-box {
  margin: 25vh 25vw;
  width: 50%;
  background-color: #eeeaea;
  display: inline-block;
  text-align: center;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  transition: 0.3s;
  padding: 2px 16px;
  border-radius: 10px;
}
#quote-box:hover {
  display: inline-block;
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
}

#text {
  font-size: 1.8rem;
}

#grid {
  display: grid;
  justify-content: center;
  align-items: center;
}
#buttons {
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding-bottom: 20px;
}
#tweet {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.button {
  background-color: #555555;
  border: none;
  color: white;
  text-shadow: 2px 2px 4px #000000;
  padding: 15px 15px;
  height: 50px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  cursor: pointer;
  outline: none;
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  animation: colorchange 50s;
  -webkit-animation: colorchange 50s; 
  animation-iteration-count: infinite;
  border-radius: 5px;
}

#new-quote:hover {
  box-shadow: 0 12px 16px 0 rgba(0, 0, 0, 0.24),
    0 17px 50px 0 rgba(0, 0, 0, 0.19);
}

#twitter {
  width: 50px;
  height: 50px;
  position: relative;
  border-radius: 5px;
  }

#twitter:hover {
  opacity: 0.8;
}

#facebook {
  width: 30px;
  height: 30px;
  position: relative;
  border-radius: 5px;
  }

#facebook:hover {
  opacity: 0.8;
}


@keyframes colorchange
{
  0%   {background: rgb(250, 57, 57);}
  25%  {background: rgb(255, 255, 64);}
  50%  {background: rgb(84, 84, 253);}
  75%  {background: rgb(98, 253, 168);}
  100% {background: rgb(250, 57, 57);}
}

@-webkit-keyframes colorchange /* Safari and Chrome - necessary duplicate */
{
  0%   {background: rgb(250, 57, 57);}
  25%  {background: rgb(255, 255, 64);}
  50%  {background: rgb(84,84, 253);}
  75%  {background: rgb(98, 253, 168);}
  100% {background: rgb(250, 57, 57);}
}